import React, { PureComponent } from 'react'
import { NavLink, withRouter, Link } from 'react-router-dom'
import moment from 'moment'
import LazyLoad from 'react-lazyload'
import offsets from 'Shared/constants/LazyLoad'
import withTranslation from 'Shared/hoc/withTranslations'
import {
  StaticRoutes,
  ExternalRoutes,
  PublicRoutes,
  CandidateRoutes
} from 'Shared/constants/Routes'
import { SocialIcons } from 'Shared/constants/Global'
import OlivLogo from 'Shared/assets/images/brand/oliv-logo.svg'
import VisaLogo from 'Shared/assets/images/logos/visa.svg'
import MasterCardLogo from 'Shared/assets/images/logos/mastercard.svg'
import LanguageService from 'Shared/services/LanguageService'
import LanguagePickerConnector from 'Shared/connectors/Layout/LanguagePickerConnector'
import LocationContactConnector from 'Shared/connectors/LocationContact/LocationContactConnector'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const currentYear = moment().year()

class NonAuthorizedFooter extends PureComponent {
  get socialIcons () {
    return SocialIcons.map(logo =>
      <span className='social-logo' key={logo.key}>
        <a href={logo.link} target='_blank' rel='noopener norefferer'>
          <i className={logo.className} />
        </a>
      </span>
    )
  }

  render () {
    const { t, isMobile } = this.props

    return (
      <div className='footer'>
        <div className='footer-divider--bigger' />
        <div className='columns'>
          <div className='column col-3 col-xs-12 col-sm-12 footer__logo-container'>
            <img src={OlivLogo} alt='Oliv logo' className='footer-logo' />
            {isMobile && <LocationContactConnector />}
          </div>
          <div className='column col-3 col-sm-6 col-xs-6'>
            <ul className='footer__list--main'>
              <li className='footer-menu__header'>
                {t('footer.candidates.jobSeekers')}
              </li>
              <li className='footer-menu__item'>
                <Link to={StaticRoutes.home}>
                  {t('footer.candidates.forJobSeekers')}
                </Link>
              </li>
              <li className='footer-menu__item'>
                <Link to={PublicRoutes.jobs}>
                  {t('footer.candidates.jobs')}
                </Link>
              </li>
              <li className='footer-menu__item'>
                <Link
                  to={CandidateRoutes.signUp}>
                  {t('footer.candidates.signUp')}
                </Link>
              </li>
              {/* <li className='footer-menu__item'>
                <a href={ExternalRoutes.blog} target='_blank'>
                  {t('footer.candidates.blog')}
                </a>
              </li> */}
            </ul>
          </div>
          <div className='column col-3 col-sm-6 col-xs-6'>
            <ul className='footer__list--main'>
              <li className='footer-menu__header'>{t('footer.employers.employers')}</li>
              <li className='footer-menu__item'>
                <NavLink exact to={StaticRoutes.employers}>
                  {t('footer.employers.forEmployers')}
                </NavLink>
              </li>
              {/* <li className='footer-menu__item'>
                <a href={ExternalRoutes.saudization} target='_blank'>
                  {t('mainNavigation.employer.saudization.heading')}
                </a>
              </li>
              <li className='footer-menu__item'>
                <a href={ExternalRoutes.customers} target='_blank'>
                  {t('footer.employers.customerStories')}
                </a>
              </li> */}
              <li className='footer-menu__item'><NavLink
                to={StaticRoutes.pricing}>{t('footer.employers.pricing')}</NavLink></li>
              {/* <li className='footer-menu__item'>
                <a href={ExternalRoutes.employerBlog} target='_blank'>
                  {t('footer.employers.employerBlog')}
                </a>
              </li> */}
            </ul>
          </div>
          <div className='column col-3 col-sm-12'>
            <div className='columns'>
              <div className='column col-12 col-sm-6 col-xs-6'>
                <ul className='footer__list--main'>
                  <li className='footer-menu__header'>{t('footer.contact.company')}</li>
                  <li className='footer-menu__item'><Link to={StaticRoutes.about}>{t('footer.contact.about')}</Link>
                  </li>
                  <li className='footer-menu__item'>
                    <a href={ExternalRoutes.careers} target='_blank'>
                      <div className='footer-menu__hiring-item'>{t('footer.contact.careers')}</div>
                    </a>
                  </li>
                  {/* <li className='footer-menu__item'>
                    <a href={ExternalRoutes.press} target='_blank'>
                      {t('footer.contact.press')}
                    </a>
                  </li> */}
                </ul>
              </div>
              <div className='column col-12 col-sm-6 col-xs-6'>
                <LazyLoad offset={offsets.small}>
                  <ul className='footer__list--main'>
                    <li
                      className='footer-menu__header footer-menu__header-payments'>{t('footer.contact.paymentMethods')}</li>
                    <li className='footer-menu__item-payments--logos'>
                      <div><img src={VisaLogo} alt='Visa logo' /></div>
                      <div><img src={MasterCardLogo} alt='MasterCard logo' /></div>
                    </li>
                  </ul>
                </LazyLoad>
              </div>
            </div>
          </div>
        </div>
        {isMobile && <LanguagePickerConnector webLayout language={LanguageService.userLanguage} /> }
        <div className='columns'>
          <div className='column col-12 col-mx-auto footer__bottom--container'>
            <LocationContactConnector />
          </div>
        </div>
        <div className='footer-divider' />
        <div className='columns footer-menu__elements'>
          <div className='column col-4 col-sm-12 footer-menu__terms-policy'>
            <span><NavLink to={StaticRoutes.terms}>{t('footer.bottom.termsOfService')}</NavLink></span>
            <span><NavLink to={StaticRoutes.privacy}>{t('footer.bottom.privacyPolicy')}</NavLink></span>
            <span><NavLink to={StaticRoutes.contact}>{t('footer.bottom.contactUs')}</NavLink></span>
          </div>
          <div className='column col-3 col-sm-12'>
            <div className='footer-menu__social-media'>
              {this.socialIcons}
            </div>
          </div>
        </div>
        <div className='columns'>
          <div className='column col-12 col-sm-12 col-mx-auto'>
            <div className='footer-menu__copyrights'>{t('footer.bottom.copyright', { year: currentYear })}</div>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslation(withDeviceWidthDetector(NonAuthorizedFooter)))



