import React from 'react'
import { cond, propEq, pipe, propOr, T } from 'ramda'
import { SUPPORT_EMAIL } from 'Shared/constants/Global'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import withTranslations from 'Shared/hoc/withTranslations'
import './LocationContact.scss'

const generateContactInfo = (introduction, contactDetail) => () => (
  <div className='location-contact'>{introduction} <span className='location-contact__highlight'>{contactDetail}</span></div>
)

const fullContact = (props) => (
  <div>
    {generateContactInfo(props.t('contact.email'), SUPPORT_EMAIL)()}{' '}
    <div className='location-contact__call'>
      {generateContactInfo(props.t('contact.phoneNumber'), props.phoneNumber)()}
    </div>
  </div>
)

const emptyInfo = () => <div />

const hasPhoneNumber = pipe(
  propOr('', 'phoneNumber'),
  isNotNilOrEmpty
)

function LocationContact (props) {
  return cond([
    [propEq('isOlivReachable', false), emptyInfo],
    [hasPhoneNumber, fullContact],
    [T, generateContactInfo(props.t('contact.email'), SUPPORT_EMAIL)]
  ])(props)
}

export default withTranslations(LocationContact)



