import { connect } from 'react-redux'
import LocationContact from 'Shared/components/LocationContact/LocationContact'
import { getLocationPhoneNumber, isOlivReachable } from 'Shared/selectors/geolocation'

const mapStateToProps = state => ({
  phoneNumber: getLocationPhoneNumber(state),
  isOlivReachable: isOlivReachable(state)
})

export default connect(mapStateToProps)(LocationContact)



