import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'

import withTranslation from 'Shared/hoc/withTranslations'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import Container from 'Shared/components/Container'

import { isStaticPage, isNoNavbarPage, isMobileRouteWithFooter } from 'Shared/helpers/routes'
import { hasAuthorizedRole } from 'Shared/helpers/User'

import NonAuthorizedFooter from './components/NonAuthorizedFooter'
import AuthorizedFooter from './components/AuthorizedFooter'

import 'Shared/layouts/Footer/Footer.scss'

class Footer extends Component {
  isStaticPage = isStaticPage(this.props.match.path)
  hasAuth = hasAuthorizedRole(this.props.user.role);
  
  renderFooter = () => {
    const { isMobile, user, location: { pathname } } = this.props

    switch (true) {
      case isNoNavbarPage(pathname):
      case isMobile && hasAuthorizedRole(user.role) && !isMobileRouteWithFooter(window.location.pathname):
        return null
      case hasAuthorizedRole(user.role):
        return <AuthorizedFooter />
      case !hasAuthorizedRole(user.role) || this.isStaticPage:
        return <NonAuthorizedFooter />
    }
  }

  render () {
    return (
      <div id='app-footer' className='footer__container parsing--hidden'>
        <Container>
          {this.renderFooter()}
        </Container>
      </div>
    )
  }
}

export default withRouter(withTranslation(withDeviceWidthDetector(Footer)))



