import React, { PureComponent } from 'react'
import moment from 'moment'
import { NavLink, withRouter } from 'react-router-dom'
import LocationContactConnector from 'Shared/connectors/LocationContact/LocationContactConnector'

import withTranslation from 'Shared/hoc/withTranslations'

import { SocialIcons } from 'Shared/constants/Global'
import { StaticRoutes } from 'Shared/constants/Routes'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const currentYear = moment().year()

class AuthorizedFooter extends PureComponent {
  get socialIcons () {
    return SocialIcons.map(logo =>
      <span className='social-logo' key={logo.key}><a href={logo.link} target='_blank' rel='noopener norefferer'><i className={logo.className} /></a></span>
    )
  }

  get WebFooter () {
    const { t } = this.props

    return (
      <div className='footer footer-logged'>
        <div className='column col-12 col-mx-auto footer__bottom--container'>
          <LocationContactConnector />
        </div>
        <div className='footer-logged__divider' />
        <div className='columns footer-logged__mobile'>
          <div className='column col-7 col-sm-12 col-mx-auto footer-logged__terms-policy'>
            <span>{t('footer.bottom.copyright', {year: currentYear})}</span>
            <div className='footer-logged__terms-bottom'>
              <span><NavLink
                to={StaticRoutes.terms}>{t('footer.bottom.termsOfService')}</NavLink></span>
              <span><NavLink
                to={StaticRoutes.privacy}>{t('footer.bottom.privacyPolicy')}</NavLink></span>
              <span><NavLink
                to={StaticRoutes.contact}>{t('footer.bottom.contactUs')}</NavLink></span>
            </div>
          </div>
          <div className='column col-5 col-sm-12 footer-logged__social-media'>
            {this.socialIcons}
          </div>
        </div>
      </div>
    )
  }

  get MobileFooter () {
    const { t } = this.props

    return (
      <div className='authorized-footer-mobile'>
        <div className='authorized-footer-mobile__section'>
          <LocationContactConnector />
        </div>
        <div className='authorized-footer-mobile__section'>
          <NavLink className='authorized-footer-mobile__link' to={StaticRoutes.terms}>{t('footer.bottom.termsOfService')}</NavLink>
          <NavLink className='authorized-footer-mobile__link' to={StaticRoutes.privacy}>{t('footer.bottom.privacyPolicy')}</NavLink>
        </div>
        <div className='authorized-footer-mobile__section'>
          {this.socialIcons}
        </div>
        <div className='authorized-footer-mobile__section'>
          <span>{t('footer.bottom.copyright', {year: currentYear})}</span>
        </div>
      </div>
    )
  }

  render () {
    const { isMobile } = this.props

    return isMobile ? this.MobileFooter : this.WebFooter
  }
}

export default withRouter(withTranslation(withDeviceWidthDetector(AuthorizedFooter)))



